.tabs {
    padding-top: 34px;
    background: #F5F5F5;

    @include media ('>=md') {
        padding-top: 48px;
    }

    @include media ('>=lg') {
        padding-top: 75px;
    }

    @include media ('>=xl') {
        margin-top: 100px;
        padding-top: 100px;
    }

    &__title {
        font-family: $font-header;
        font-weight: 400;
        color: $color-title;
        font-size: 25px;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;

        @include media ('>=md') {
            font-size: 30px;
            line-height: 35px;
            margin-bottom: 29px;
        }

        @include media ('>=lg') {
            font-size: 40px;
            line-height: 50px;
            margin-bottom: 40px;
        }

        @include media ('>=xl') {
            font-size: 50px;
            line-height: 60px;
        }
    }

    &__header {
        list-style-type: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        @include media ('>=lg') {
            margin-bottom: 68px;
        }
    }

    &__btn {
        font-size: 14px;
        line-height: 15px;
        font-family: $font-header;
        color: #4567AA;
        transition: 0.5s;
        text-transform: uppercase;
        border: 1px solid #4567AA;
        padding: 10px 20px;
        margin-right: 10px;
        margin-bottom: 10px;

        @include media ('>=md') {
            font-size: 16px;
            line-height: 17px;
            margin-right: 20px;
        }

        @include media ('>=lg') {
            font-size: 24px;
            line-height: 26px;
            padding: 15px 40px;
            margin-right: 30px;
        }

        @include media ('>=xl') {
            font-size: 28px;
            line-height: 30px;
            margin-right: 45px;
        }

        &:hover {
            cursor: pointer;
        }

    }

    &__content {

        display: none;

    }

    &__card {
        width: 340px;
        margin-bottom: 60px;
        position: relative;

        @include media ('>=md') {
            width: 243px;
        }

        @include media ('>=lg') {
            width: 407px;
        }

        @include media ('>=xl') {
            width: 714px;

        }
    }

    &__card img {
        width: 100%;
        object-fit: cover;
        border-radius: 0 100px 0 0;
        height: 435px;

        @include media ('>=md') {
            height: 243px;
        }

        @include media ('>=lg') {
            height: 460px;
        }

        @include media ('>=xl') {
            height: 475px;

        }
    }

    &__link {
        position: absolute;
        background-color: #F5F5F5;
        ;
        bottom: 0;
        right: 0;
        width: 221px;
        padding: 21px 0;
        padding-left: 21px;
        border-radius: 50px 0 0 0;
    }

    &__link a {
        font-family: $font-text;
        color: $color-text;
        font-size: 18px;
        line-height: 24px;
        font-weight: 400;
        text-decoration: none;
        position: relative;

        &::after {
            position: absolute;
            top: -56%;
            right: -70%;
            content: '\e801';
            font-family: 'fontello';
            font-size: 16px;
            border: 1px solid #FF8139;
            color: #fff;
            text-align: center;
            background-color: #FF8139;
            padding-top: 5px;
            border-radius: 50%;
            width: 45px;
            height: 45px;
        }

    }
}

.tabs__card--vindserfing {
    @include media ('>=md') {
        width: 350px;
        margin-right: 30px;
    }

    @include media ('>=lg') {
        width: 750px;
        margin-right: 30px;
    }

    @include media ('>=xl') {
        width: 900px;
        margin-right: 30px;

    }
}

.tabs__card--safari {
    @include media ('>=md') {
        width: 350px;
        margin-left: 30px;
    }

    @include media ('>=lg') {
        width: 750px;
        margin-left: 30px;
    }

    @include media ('>=xl') {
        width: 900px;
        margin-left: 30px;
    }
}

.tabs__btn--active {
    background-color: #4567AA;
    color: #fff;
}

.tabs__content--active {
    display: block;
    display: flex;
    flex-wrap: wrap;
}