.special {
    padding-top: 75px;

    @include media ('>=md') {
        padding-top: 80px;
    }

    @include media ('>=lg') {
        padding-top: 120px;
    }

    @include media('>=xl') {
        padding-top: 144px;
    }

    &__title {
        font-family: $font-header;
        color: $color-title;
        font-size: 25px;
        line-height: 30px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;

        @include media ('>=md') {
            font-size: 30px;
            line-height: 35px;
        }

        @include media ('>=lg') {
            font-size: 40px;
            line-height: 50px;
        }

        @include media ('>=xl') {
            font-size: 50px;
            line-height: 60px;
            margin-bottom: 122px;
        }
    }

    &__inner {
        max-width: 561px;
        margin: 0 auto;
        position: relative;


        &::before {
            position: absolute;
            top: 0;
            bottom: 0;
            left: -12%;
            content: url('../img/bubbles-left.png');
            width: 26px;
            height: 45px;

            @include media ('>=md') {
                left: 0%;
            }

            @include media ('>=lg') {
                left: 0;
                content: url('../img/bubbles-left.png');
                height: 85px;
            }

            @include media ('>=xl') {
                left: -50px;
            }
        }

        &::after {
            position: absolute;
            top: 0;
            right: 0;
            content: url('../img/bubbles-right.png');
            width: 26px;
            height: 45px;

            @include media ('>=md') {
                right: 7%;
            }

            @include media ('>=lg') {
                right: 6%;
                content: url('../img/bubbles-right.png');
                height: 100%;
            }

            @include media ('>=xl') {
                right: -2%;
            }
        }
    }
}

.special-img {
    width: auto;
    height: auto;

    @include media ('>=md') {

        margin-right: 30px;
    }

    @include media ('>=xl') {
        margin-right: 85px;
    }

}

.special-img img {
    width: 100%;
    height: 100%;

    @include media ('>=md') {
        height: 271px;
        width: 324px;
    }

    @include media ('>=lg') {
        height: 300px;
        width: 555px;
    }

    @include media ('>=xl') {
        width: 640px;
        height: 320px;
    }
}

.special__item {
    position: relative;
    margin-bottom: 62px;

    &::after {
        position: absolute;
        bottom: -31px;
        left: 0;
        right: 0;
        height: 1px;
        background-color: #4567AA;
        content: '';
    }
}

.special-date {
    font-family: $font-text;
    font-weight: 400;
    color: $color-title;
    font-size: 16px;
    line-height: 22px;

    display: block;
    padding-top: 22px;
}

.special-right__title {
    font-family: $font-header;
    color: $color-title;
    font-size: 20px;
    line-height: 30px;
    font-weight: 500;
    text-transform: uppercase;

    @include media ('>=lg') {
        font-size: 24px;
        line-height: 26px;
    }

    @include media ('>=xl') {
        font-size: 30px;
        line-height: 35px;
    }
}

.special-right__text {
    font-family: $font-text;
    color: $color-text;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    max-width: 505px;

    @include media ('>=lg') {
        margin-bottom: 79px;
    }

    @include media ('>=xl') {
        font-size: 20px;
        line-height: 28px;
    }
}

.special-right__link {
    position: relative;

    &::after {
        position: absolute;
        top: 0;
        right: 65%;
        content: '\e801';
        font-family: 'fontello';
        color: #4567AA;
    }
}

.special-right__link a {
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    text-transform: uppercase;
    text-decoration: none;
    color: #4567AA;
}