.video {
    padding-top: 60px;
padding-bottom: 70px;
position: relative;
&::after{
    position: absolute;
    bottom: -50px;
    width: 100%;
    height: 1px;
    background-color: #4567AA;
}
    @include media ('>=md') {
        padding-top: 84px;
    }

    @include media (">=lg") {
        padding-top: 101px;
        padding-bottom: 70px;
    }

    @include media ('>=xl') {
        padding-top: 162px;
        padding-bottom: 350px;
    }

    &__title {
        font-family: $font-header;
        font-size: 25px;
        line-height: 30px;
        color: $color-title;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 34px;

        @include media ('>=md') {
            font-size: 30px;
            line-height: 35px;
        }

        @include media ('>=lg') {
            font-size: 40px;
            line-height: 50px;
            margin-bottom: 55px;
        }

        @include media ('>=xl') {
            font-size: 50px;
            line-height: 55px;
            margin-bottom: 78px;
        }
    }

    &__item {
        max-width: 768px;
        margin: 0 auto;
        position: relative;

        &::before {
            position: absolute;
            top: 0;
            left: 0;
            content: url('../img/video-before.svg');
        }

        &::after {
            position: absolute;
            top: 0;
            right: 0;
            content: url('../img/video-after.svg');
        }
    }
}

.video-wrap{
    height: 343px;

    img{
        @include img-cover;
    }

    
}

.video-link{
    position: relative;
    display: flex;
    justify-content: center;
    &::after{
        position: absolute;
        content: url('../img/play-video.svg');
        align-self: center;
    }

    
}

