.home-wrapper {
    background-image: url('../img/bg.png');
    height: 475px;
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    position: relative;

    @include media ('>=md') {
        height: 400px;

    }

    @include media ('>=lg') {
        height: 597px;
    }

    @include media ('>=xl') {
        height: 816px;
    }
}

.home-title {
    font-family: $font-header;
    font-size: 36px;
    line-height: 39px;
    color: #fff;
    text-align: center;
    padding-top: 96px;
    text-transform: uppercase;

    @include media ('>=md') {
        font-size: 46px;
        line-height: 50px;
    }

    @include media ('>=lg') {
        font-size: 70px;
        line-height: 77px;
        width: 733px;
        margin: 0 auto;
    }

    @include media ('>=xl') {
        width: 977px;
        margin: 0 auto;
        font-size: 90px;
        line-height: 99px;
        padding-top: 196px;
    }
}

.label-date {
    font-family: $font-text;
    font-weight: 500;
    font-size: 10px;
    line-height: 13px;
    color: $color-text;
    margin-left: 20px;
    margin-top: 5px;
    text-transform: uppercase;

    @include media ('>=lg') {
        font-size: 16px;
        line-height: 22px;
        margin-left: 50px;
        margin-top: 15px;
    }
}

.date {
    position: relative;
    margin-right: 11px;

    &::after {
        position: absolute;
        top: 7px;
        left: 0;
        content: '\e80a';
        font-family: 'fontello';
        font-size: 20px;

        @include media ('>=lg') {
            top: 20px;
            left: 20px;
        }
    }

    &::before {
        position: absolute;
        content: '';
        height: 100%;
        width: 1px;
        background-color: #4567AA;
        right: -15px;
        top: 0;
    }

}


.panel {
    display: none;

    @include media ('>=md') {
        display: block;
        display: flex;
        justify-content: space-between;
        width: 621px;
        background: #FFFFFF;
        border-radius: 2px;
        position: absolute;
        bottom: 55px;
        left: 2%;
        padding-left: 17px;

        &__date {
            background: #EEEEEE;
            border: 1px solid transparent;
            border-radius: 50%;
            position: relative;
            width: 40px;
            height: 40px;
            margin-left: 10px;

            @include media ('>=lg') {
                width: 45px;
                height: 45px;
            }

            &::after {
                position: absolute;
                top: 15px;
                left: 10px;
                content: '\e809';
                font-family: 'fontello';
                font-size: 10px;
            }


        }
    }

    @include media ('>=lg') {
        width: 977px;
        bottom: 111px;
        left: 10%;
        height: 70px;
    }

    @include media ('>=xl') {
        width: 1111px;
        bottom: 111px;
        height: 86px;
        left: 17%;
        height: 70px;
    }

}

.peoples {
    margin-left: 17px;
    position: relative;

    &::after {
        position: absolute;
        top: 0;
        right: -15px;
        content: '';
        height: 100%;
        width: 1px;
        background-color: #4567AA;

        @include media ('>=lg') {
            right: -60px;
        }
    }

    @include media ('>=lg') {
        margin-left: 0;
    }
}

.label-people {
    font-family: $font-text;
    font-weight: 500;
    font-size: 10px;
    line-height: 13px;
    color: $color-text;
    text-transform: uppercase;
    align-items: center;

    @include media ('>=lg') {
        font-size: 16px;
        line-height: 22px;
    }
}

.people-icon {
    position: relative;
    margin-left: 27px;

    &::after {
        position: absolute;
        top: 6px;
        left: -22px;
        content: '\e80d';
        font-family: 'fontello';

        @include media ('>=lg') {
            font-size: 20px;
            top: 0px;
            left: -68px;

        }
    }

    @include media ('>=lg') {
        margin-left: -9px;
        margin-top: 10px;
    }
}

.plus {
    background: #EEEEEE;
    border: 1px solid transparent;
    border-radius: 50%;
    position: relative;
    width: 40px;
    height: 40px;
    margin-left: 10px;

    &::after {
        position: absolute;
        top: 12px;
        left: 13px;
        content: '\e800';
        font-family: 'fontello';
    }

    @include media ('>=lg') {
        width: 45px;
        height: 45px;
    }
}

.minus {
    background: #EEEEEE;
    border: 1px solid transparent;
    border-radius: 50%;
    position: relative;
    width: 40px;
    height: 40px;
    margin-left: 10px;

    &::after {
        position: absolute;
        top: 19px;
        left: 13px;
        content: '\e80c';
        font-family: 'fontello';
        font-size: 1px;
    }

    @include media ('>=lg') {
        width: 45px;
        height: 45px;
    }
}

.input-people {
    width: 25px;
    height: 40px;
    border: none;
    padding: 0;
    font-size: 16px;
    font-family: $font-text;
}

.search__btn {
    width: 45px;
    height: 45px;
    background-color: #4567AA;
    position: relative;
    border-radius: 50%;
    border: none;
    margin-left: 30px;
    margin-top: 4px;

    &::after {
        position: absolute;
        top: 5px;
        left: 10px;
        content: '\e802';
        font-family: 'fontello';
        color: #fff;
        font-size: 22px;

    }

    @include media ('>=lg') {
        margin-right: 70px;
        width: 50px;
        height: 50px;
        margin-top: 10px;
    }
}