.site-header {
    height: 40px;

    @include media ('>=md') {
        height: 55px;
    }

    @include media ('>=xl') {
        height: 69px;
    }
}

.header__logo {
    position: relative;

    &::after {
        position: absolute;
        top: 9px;
        left: -15px;
        width: 30px;
        content: url('../img/line-header.png');
    }

    &::before {
        position: absolute;
        top: 0;
        left: -15px;
        width: 30px;
        content: url('../img/line-header.png');
    }
}

.header__inner {
    display: flex;
    justify-content: space-between;
}

.bron {
    background-color: #4567AA;
    width: 54px;
    height: 40px;
    margin-left: 20px;
    position: relative;
    transition: 0.3s;

    @include media ('>=md') {
        width: 206px;
        height: 55px;
    }

    @include media ('>=xl') {
        height: 69px;
    }

    &::after {
        position: absolute;
        top: 5px;
        left: 17px;
        content: '\e801';
        font-family: 'fontello';
        color: #fff;

        @include media ('>=md') {
            left: 170px;
            top: 15px;
        }
    }

    &:hover {
        background: #FF8139;
        transition: 0.3s;
    }
}

.bron span {
    display: none;

    @include media ('>=md') {
        display: block;
        font-family: $font-text;
        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        color: #fff;
        padding-left: 27px;
        padding-top: 18px;
    }
}

.logo {
    display: none;

}