.about {
    &__title {
        font-family: $font-header;
        font-size: 25px;
        line-height: 30px;
        text-align: center;
        color: $color-title;
        font-weight: 400;
        text-transform: uppercase;
        padding-top: 25px;

        @include media ('>=md') {
            font-size: 30px;
            line-height: 50px;
        }

        @include media ('>=lg') {
            font-size: 40px;
            margin-bottom: 60px;
        }
    }

    @include media ('>=lg') {
        padding-top: 100px;
    }
}

.slider-wrap {
    position: relative;
    width: 329px;
    padding-bottom: 50px;

    @include media ('>=md') {
        width: 354px;
        margin-left: 19px;

    }


}

.about-slider {}

.item {
    width: 329px;
    margin-right: 40px;
    border-radius: 100px 0 0 0;
    display: none;

    @include media ('>=md') {
        height: 375px;
    }

    @include media ('>=lg') {
        width: 499px;
        height: 450px;
    }

    @include media ('>=xl') {
        width: 720px;
        height: 600px;
    }
}

.item.active {
    display: block;
}

.item img {
    width: 100%;
    border-radius: 100px 0 0 0;
}

.nav {
    position: absolute;
    bottom: 0;
    left: 28%;
    display: flex;

    @include media ('>=md') {
        bottom: 12%;
        left: -98%;
    }

    @include media ('>=lg') {
        bottom: 18%;
        left: -155%;
    }
}

.nav span {
    font-size: 22px;
    font-family: $font-header;
    color: #4567AA;
    line-height: 24px;

    @include media ('>=lg') {
        font-size: 40px;
        line-height: 44px;
    }

    @include media ('>=xl') {
        font-size: 50px;
        line-height: 52px;
    }
}

.next {
    background-color: transparent;
    border: 1px solid #4567AA;
    border-radius: 50%;
    height: 45px;
    width: 45px;
    display: flex;
    margin-left: 12px;
}

.i-next {
    position: relative;
    margin: 0;
    display: flex;

    &::after {
        position: absolute;
        align-self: center;
        content: '\e808';
        font-family: 'fontello';
        color: #4567AA;
        font-size: 12px;
    }
}

.prev {
    background-color: transparent;
    border: 1px solid #4567AA;
    border-radius: 50%;
    height: 45px;
    width: 45px;
    display: flex;
    margin-right: 12px;
}

.i-prev {
    position: relative;
    margin: 0;
    display: flex;

    &::after {
        position: absolute;
        align-self: center;
        content: '\e807';
        font-family: 'fontello';
        color: #4567AA;
        font-size: 12px;

    }
}

.about__links {
    width: 330px;

    @include media ('>=md') {
        padding-top: 70px;
    }

    @include media ('>=lg') {
        width: 570px;
        margin-right: 40px;
    }

    @include media ('>=xl') {
        width: 679px;
    }
}

.about-item {
    padding-left: 19px;
    margin-bottom: 40px;
    position: relative;
    transition: 0.5s;


    &::before {
        position: absolute;
        bottom: -10px;
        right: 0;
        left: 0;
        height: 1px;
        content: '';
        background: #4567AA;
    }

    &:hover {
        margin-left: 10px;
        transition: 0.5s;
    }

    &:hover a {
        color: #4567AA;
        transition: 0.5s;
    }

    &:hover a::after {
        color: #4567AA;
        transition: 0.5s;
    }

    @include media ('>=lg') {
        margin-bottom: 40px;
    }

    @include media ('>=xl') {
        margin-bottom: 60px;
    }
}

.about-item:nth-child(1)::marker {
    content: url('../img/01.svg');
    height: 35px;
    width: 31px;

}

.about-item:nth-child(2)::marker {
    content: url('../img/02.svg');
    height: 35px;
    width: 31px;
}

.about-item:nth-child(3)::marker {
    content: url('../img/03.svg');
    height: 35px;
    width: 31px;
}

.about-item:nth-child(4)::marker {
    content: url('../img/04.svg');
    height: 35px;
    width: 31px;
}

.about-item a {
    text-transform: uppercase;
    text-decoration: none;
    font-size: 22px;
    line-height: 24px;
    color: $color-title;
    font-family: $font-header;
    font-weight: 400;
    display: block;
    position: relative;
    max-width: 100%;
    transition: 0.5s;

    @include media ('>=lg') {
        font-size: 30px;
        line-height: 33px;

        &::after {
            position: absolute;
            top: 0;
            right: 0;
            content: 'ПОДРОБНЕЕ';
            font-size: 16px;
            text-transform: uppercase;
            font-family: $font-text;
            color: $color-title;
            transition: 0.5s;
        }
    }

    &::after {
        position: absolute;
        top: 0;
        right: 0;
        content: '\e801';
        font-family: 'fontello';
        color: $color-title;
        transition: 0.5s;
    }
}