.rooms-wrapper{
    background-image: url('../img/background-rooms.jpg');
    height: 300px;
    background-size: cover;
    background-position: right;
    @include media ('>=md'){
        height: 250px;
        background-size: cover;
        background-position: right;
        position: relative;
    }
    @include media ('>=lg'){
        height: 380px;
    }
    @include media ('>=xl'){
        background-image: url('../img/background-rooms-desktop.jpg');
        height: 445px;
    }
}
.rooms__item{
    @include media ('>=lg'){
        margin-top: 40px;
    }
}
.rooms-panel{
    display: none;

    @include media ('>=md') {
        display: block;
        display: flex;
        justify-content: space-between;
        width: 621px;
        background: #FFFFFF;
        border-radius: 2px;
        position: absolute;
        bottom: 34px;
        left: 2%;
        padding-left: 17px;

        &__date {
            background: #EEEEEE;
            border: 1px solid transparent;
            border-radius: 50%;
            position: relative;
            width: 40px;
            height: 40px;
            margin-left: 10px;

            @include media ('>=lg') {
                width: 45px;
                height: 45px;
            }

            &::after {
                position: absolute;
                top: 15px;
                left: 10px;
                content: '\e809';
                font-family: 'fontello';
                font-size: 10px;
            }


        }
    }

    @include media ('>=lg') {
        width: 977px;
        bottom: 50px;
        left: 10%;
        height: 70px;
    }

    @include media ('>=xl') {
        width: 1111px;
        bottom: 52px;
        
        left: 17%;
        height: 80px;
    }

}
.rooms__inner{
    margin: 0 auto;
    padding-top: 15px;
    @include media ('>=lg'){
        margin-top: 70px;
    }
}
.rooms__items{
    background: #F5F5F5;
    width: 288px;
    border-radius: 70px 70px 0px 0px;
    margin-bottom: 45px;
    @include media ('>=md'){
        margin-left: 70px;
    }
    @include media ('>=lg'){
        width: 408px;
        height: 455px;
        margin-left: 200px;
    }
    @include media ('>=xl'){
        width: 580px;
        height: 658px;
    }
}
.rooms-img{
    width: auto;
}
.rooms-img img{
    width: 100%;
    height: auto;
    border-radius: 39px 39px 0px 0px;
    padding: 26px 26px 0 26px;
    margin-bottom: 24px;
    @include media ('>=lg'){
        border-radius: 50px 50px 0px 0px;
        padding: 40px 34px 0 34px;
    }
}
.rooms__items:nth-child(1){
    @include media ('>=xl'){
        margin-left: 350px;
    }
}
.rooms__items:nth-child(3){
    @include media ('>=xl'){
        margin-left: 350px;
    }
}