.relaxation__item {
    background-image: url('../img/background-rooms.jpg');
    height: 360px;
    background-size: cover;
    background-position: right;
    margin-bottom: 50px;
    position: relative;

    @include media ('>=md') {
        height: 400px;
    }

    @include media ('>=lg') {
        height: 550px;
    }
}

.relaxation__title {
    font-size: 25px;
    line-height: 30px;
    font-weight: 400;
    font-family: $font-header;
    color: $color-title;
    background: #FFFFFF;
    border-top: 1px solid #fff;
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
    border-radius: 100px 100px 0 0;
    text-transform: uppercase;
    text-align: center;
    max-width: 640px;
    
    
    padding-left: 41px;
    padding-right: 41px;
    position: absolute;
    bottom: -32px;
    @include media ('>=md') {
        font-size: 30px;
        line-height: 50px;
        left: 22%;
    }

    @include media ('>=lg') {
        font-size: 40px;
        left: 31%;
        padding-top: 27px;
        padding-left: 93px;
        padding-right: 93px;
    }

    @include media ('>=xl') {
        font-size: 50px;
        line-height: 55px;
        padding-top: 50px;
        padding-left: 110px;
        padding-right: 110px;
    }
}