.rooms {
    padding-top: 54px;

    @include media ('>=md') {
        padding-top: 72px;
    }

    @include media ('>=xl') {
        padding-top: 135px;
    }

    &__title {
        font-size: 25px;
        font-family: $font-header;
        color: $color-title;
        line-height: 30px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;

        @include media ('>=lg') {
            font-size: 40px;
            line-height: 50px;
        }

        @include media ('>=xl') {
            font-size: 50px;
            line-height: 55px;
        }
    }

    &__subtitle {
        margin-top: -35px;
        text-align: center;
        font-family: $font-text;
        color: $color-title;
        font-size: 15px;

        @include media ('>=md') {
            font-size: 18px;
            line-height: 25px;
            margin-top: -25px;
        }

        @include media ('>=lg') {
            font-size: 36px;
            line-height: 26px;
        }

        @include media ('>=xl') {
            font-size: 30px;
            line-height: 42px;
        }
    }

    &__item {
        position: relative;
        height: 85px;

        &::before {
            position: absolute;
            top: 0;
            left: 44px;
            content: url('../img/bubbles-left.png');
            width: 26px;
            height: 45px;

            @include media ('>=md') {
                left: 25%;
            }

            @include media ('>=lg') {
                left: 31%;
                content: url('../img/bubbles-left.png');
                height: 85px;
            }
        }

        &::after {
            position: absolute;
            top: 0;
            right: 16%;
            content: url('../img/bubbles-right.png');
            width: 26px;
            height: 45px;

            @include media ('>=md') {
                right: 31%;
            }

            @include media ('>=lg') {
                right: 34%;
                content: url('../img/bubbles-right.png');
                height: 100%;
            }
        }
    }
}


// SLIDER
.slider-room {
    margin-top: 53px;
    position: relative;

}

.slide-room {
    background: #F5F5F5;
    border-radius: 100px 100px 0 0;
    width: 288px;

    @include media('>=md') {
        margin-right: 41px;
        width: 288px;

    }

    @include media ('>=lg') {
        width: 408px;
    }

    @include media ('>=xl') {
        width: 580px;
    }
}

.slide-img {
    width: auto;
}

.slide-img img {
    width: 100%;
    height: auto;
    padding: 28px;
    border-radius: 100px 100px 0 0;
}

.slide-info {
    padding-left: 28px;
    margin-bottom: 21px;
}

.slide-info span {
    font-family: $font-text;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: #4567AA;
    ;
    text-transform: uppercase;
    margin-right: 12px;

    @include media ('>=lg') {
        font-size: 16px;
    }

    @include media ('>=xl') {
        font-size: 20px;
    }
}

.icon {
    padding-left: 24px;
    position: relative;

    &::before {
        position: absolute;
        top: -2px;
        left: 0;
        content: '\e80d';
        font-family: 'fontello';
        color: #4567AA;
    }
}

.slide-buttons {
    display: flex;
    justify-content: center;
    padding-bottom: 25px;

    @include media ('>=lg') {
        justify-content: start;
    }
}

.blue {
    font-size: 11px;
    font-family: $font-text;
    color: #F5F5F5;
    text-transform: uppercase;
    background-color: #4567AA;
    border-radius: 2px;
    border: 1px solid transparent;
    outline: none;
    padding: 12px 21px;
    margin-right: 11px;
    transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
    position: relative;

    @include media ('>=lg') {
        margin-right: 24px;
        margin-left: 28px;
        font-size: 14px;
    }

    @include media ('>=xl') {
        padding: 20px 41px;
        font-size: 18px;
    }

}

.blue:hover {
    background-color: transparent;
    color: #4567AA;
    transition: 0.5s;
    border: 1px solid #4567AA;
}

.transparent {
    font-size: 11px;
    font-family: $font-text;
    color: #4567AA;
    text-transform: uppercase;
    background-color: transparent;
    border-radius: 2px;
    border: 1px solid #4567AA;
    outline: none;
    padding: 12px 21px;

    @include media ('>=lg') {
        font-size: 14px;
    }

    @include media ('>=xl') {
        padding: 20px 41px;
        font-size: 18px;
    }

    &:hover {
        background-color: #4567AA;
        border: 1px solid transparent;
        color: #fff;
        transition: 0.5s;
    }
}

.next-btn {
    background: #FF8139;
    border-radius: 50%;
    border: none;
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 30%;
    right: -8%;

    @include media ('>=md') {

        right: 19%;
    }

    @include media ('>=lg') {
        width: 60px;
        height: 60px;
        right: 27%;
        top: 45%;
    }

    @include media ('>=xl') {
        width: 70px;
        height: 70px;
    }
}

.icon-next {
    position: relative;
    align-self: center;

    &::after {
        position: absolute;
        top: -5px;
        left: -10px;
        content: '\e808';
        font-family: 'fontello';
        color: #fff;
        font-size: 10px;

        @include media ('>=lg') {
            font-size: 15px;
            top: -10px;
            left: -10px;
        }
    }
}

.prev-btn {
    background: #FF8139;
    border-radius: 50%;
    border: none;
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 30%;
    left: -8%;
    z-index: 1;

    @include media ('>=md') {
        left: 18%;
    }

    @include media ('>=lg') {
        width: 60px;
        height: 60px;
        left: 27%;
        top: 45%;
    }

    @include media ('>=xl') {
        width: 70px;
        height: 70px;
    }
}

.icon-prev {
    position: relative;
    align-self: center;

    &::after {
        position: absolute;
        top: -5px;
        left: -10px;
        content: '\e807';
        font-family: 'fontello';
        color: #fff;
        font-size: 10px;

        @include media ('>=lg') {
            font-size: 15px;
            top: -10px;
            left: -10px;
        }
    }
}